import axios from 'axios';

const API_BASE_URL = 'https://bigbrotherv01.runasp.net/api';

interface CourseData {
  name: string;
  startFrom: string;
  endIn: string;
  dayOfCourse: number;
  assistantId: number;
}

interface AddCourseResponse {
  // Define the expected response structure based on your API
  id?: number;
  message?: string;
  // Add other fields as needed
}

interface Course {
  id: number;
  name: string;
  startFrom: string;
  endIn: string;
  dayOfCourse: number;
  assistantId: number;
  // Add other fields that might be returned by the API
}

export const addCourse = async (courseData: CourseData): Promise<AddCourseResponse> => {
  try {
    const payload = {
      id: 0, // As specified in the API
      name: courseData.name,
      startFrom: courseData.startFrom,
      endIn: courseData.endIn,
      dayOfCourse: courseData.dayOfCourse,
      instructorId: courseData.assistantId // Map assistantId to instructorId for the API payload
    };

    const response = await axios.post(
      `${API_BASE_URL}/Instructor/add-course`,
      payload,
      {
        headers: {
          'accept': '*/*',
          'Content-Type': 'application/json'
        }
      }
    );

    return response.data;
  } catch (error: any) {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      throw new Error(error.response.data?.message || 'Failed to add course. Please check your data.');
    } else if (error.request) {
      // The request was made but no response was received
      throw new Error('Network error. Please check your internet connection.');
    } else {
      // Something happened in setting up the request that triggered an Error
      throw new Error('An unexpected error occurred. Please try again.');
    }
  }
};

export const getAllCourses = async (assistantId: number): Promise<Course[]> => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/Course/all/${assistantId}`,
      {
        headers: {
          'accept': '*/*'
        }
      }
    );

    return response.data;
  } catch (error: any) {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      throw new Error(error.response.data?.message || 'Failed to fetch courses.');
    } else if (error.request) {
      // The request was made but no response was received
      throw new Error('Network error. Please check your internet connection.');
    } else {
      // Something happened in setting up the request that triggered an Error
      throw new Error('An unexpected error occurred. Please try again.');
    }
  }
}; 