import axios from 'axios';

const API_BASE_URL = 'https://bigbrotherv01.runasp.net/api';

interface AttendanceRecord {
  date: string;
  time: string | null;
  studentName: string;
  studentId: number;
  courseId: number;
  asisstantId: number;
}

interface AttendanceResponse {
  result: any;
  value: {
    items: AttendanceRecord[];
    // Add pagination fields if needed
  };
}

export const getCourseAttendance = async (
  courseId: number, 
  authToken: string,
  pageNumber: number = 1, 
  pageSize: number = 20
): Promise<AttendanceRecord[]> => {
  try {
    const response = await axios.get<AttendanceResponse>(
      `${API_BASE_URL}/Attendance/for-course`,
      {
        params: {
          courseId,
          PageNumber: pageNumber,
          PageSize: pageSize
        },
        headers: {
          'accept': '*/*',
          'Authorization': `Bearer ${authToken}` // Add auth token
        }
      }
    );

    return response.data.value.items;
  } catch (error: any) {
    if (error.response) {
      throw new Error(error.response.data?.message || 'Failed to fetch attendance data.');
    } else if (error.request) {
      throw new Error('Network error. Please check your internet connection.');
    } else {
      throw new Error('An unexpected error occurred. Please try again.');
    }
  }
};

export const downloadCourseAttendanceFromData = (
  attendance: AttendanceRecord[],
  courseId: number,
  courseName: string
): void => {
  try {
    // Create CSV content
    const headers = ['Student Name', 'Student ID', 'Course ID', 'Course Name', 'Attendance Date', 'Attendance Time', 'Assistant ID'];
    
    const csvContent = [
      // Add headers
      headers.join(','),
      // Add data rows
      ...attendance.map(record => [
        `"${record.studentName}"`, // Wrap in quotes to handle names with commas
        record.studentId,
        record.courseId,
        `"${courseName}"`,
        new Date(record.date).toLocaleDateString(),
        new Date(record.date).toLocaleTimeString(),
        record.asisstantId
      ].join(','))
    ].join('\n');

    // Create blob and download
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    
    // Generate filename with current date
    const currentDate = new Date().toISOString().split('T')[0];
    const filename = `${courseName.replace(/[^a-zA-Z0-9]/g, '_')}_Attendance_${currentDate}.csv`;
    
    link.setAttribute('download', filename);
    document.body.appendChild(link);
    link.click();
    link.remove();
    window.URL.revokeObjectURL(url);
  } catch (error) {
    throw new Error('Failed to generate attendance file.');
  }
}; 