import axios from 'axios';

const API_BASE_URL = 'https://bigbrotherv01.runasp.net/api';

interface LoginResponse {
  // Define the expected response structure based on your API
  token?: string;
  accessToken?: string; // Some APIs use different token field names
  authToken?: string;
  user?: any;
  email?: string;
  instructorId?: number;
  assistantid?: number; // The actual field returned by the API
  id?: number; // Some APIs return 'id' instead of 'instructorId'
  // Add other fields as needed
}

export const loginInstructor = async (email: string, password: string): Promise<LoginResponse> => {
  try {
    const response = await axios.post(
      `${API_BASE_URL}/Accounts/login-instractour`,
      {
        email,
        password
      },
      {
        headers: {
          'accept': 'text/plain',
          'Content-Type': 'application/json'
        }
      }
    );

    // Debug: Log the actual response to see what fields are returned
    console.log('Login API Response:', response.data);
    console.log('Available fields and values:');
    if (response.data) {
      Object.keys(response.data).forEach(key => {
        console.log(`  ${key}: ${response.data[key]}`);
      });
    }

    // Try to extract assistantId from various possible field names
    const possibleIds = [
      response.data?.assistantid,
      response.data?.assistantId, 
      response.data?.AssistantId,
      response.data?.assistant_id,
      response.data?.instructorId,
      response.data?.InstructorId,
      response.data?.instructor_id,
      response.data?.id,
      response.data?.Id,
      response.data?.userId,
      response.data?.UserId,
      response.data?.user_id
    ];

    console.log('Checking possible ID fields:', possibleIds);
    const extractedId = possibleIds.find(id => id && id > 0);

    // Return the response data along with the email for navigation purposes
    const result = {
      ...response.data,
      email: email, // Include the email in the response for user display
      // Extract instructor ID from response (try multiple possible field names)
      instructorId: extractedId || null,
      // Extract authentication token for API calls
      authToken: response.data?.token || response.data?.accessToken || response.data?.authToken || null
    };

    // Debug: Log what we're extracting
    console.log('Final extracted instructorId:', result.instructorId);

    return result;
  } catch (error: any) {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      throw new Error(error.response.data?.message || 'Login failed. Please check your credentials.');
    } else if (error.request) {
      // The request was made but no response was received
      throw new Error('Network error. Please check your internet connection.');
    } else {
      // Something happened in setting up the request that triggered an Error
      throw new Error('An unexpected error occurred. Please try again.');
    }
  }
}; 