import axios from 'axios';

const API_BASE_URL = 'https://bigbrotherv01.runasp.net/api';

export interface Student {
  id: number;
  name: string;
  image?: string | null;
  email?: string;
}

export const getStudentsByCourse = async (courseId: number, authToken?: string | null): Promise<Student[]> => {
  try {
    const response = await axios.get<Student[]>(`${API_BASE_URL}/Student/by-course/${courseId}`, {
      headers: {
        accept: '*/*',
        ...(authToken ? { Authorization: `Bearer ${authToken}` } : {})
      }
    });

    return response.data;
  } catch (error: any) {
    if (error.response) {
      throw new Error(error.response.data?.message || 'Failed to fetch students.');
    }
    if (error.request) {
      throw new Error('Network error. Please check your internet connection.');
    }
    throw new Error('An unexpected error occurred.');
  }
}; 